.site-footer {
	border-top: 1px solid $colour-backgrounds-light-gray;
	padding: 1em;
	text-align: center;
	.qul-logo img {
		max-width: 400px;
		width: 100%;
	}
	.qu-logo img {
		max-width: 175px;
	}
}

@media screen and (min-width: $query__small) {
	.qul-logo {
		float: left;
	}
	.qu-logo {
		float: right;
	}
}