.header-image {
	margin: 0;
	img {
		display: block;
		width: 100%;
	}
}

.site-header {
	border-bottom: 1px solid $colour-backgrounds-light-gray;
	display: grid;
	grid-template-columns: auto;
	align-items: center;
}

.site-branding {
	padding: 1em;
	.site-title {
		margin: 0;
		padding: 0;
		font-weight: bold;
		font-size: 1.6em;
		line-height: 1em;
		a {
			text-decoration: none;
			color: $colour-primary-blue;
			&:hover,
			&:focus {
				text-decoration: underline;
				color: $colour-primary-blue;
			}
		}
	}
	.site-description {
		margin: 0;
		padding: 0.5em 0 0 0;
		font-size: 1em;
		font-style: italic;
	}
}

@media screen and (min-width: $query__small) {
	.site-header {
		grid-template-columns: auto;
	}
}

@media screen and (min-width: $query__medium) {
	.site-header {
		grid-template-columns: auto 1fr;
	}
	.site-branding {
		grid-column: 1;
	}
	.main-navigation {
		grid-column: 2;
	}
}
