.main-navigation {
	padding: 1em;
	ul {
		display: none;
		list-style: none;
		margin: 0;
		padding-left: 0;
		text-transform: uppercase;
		font-weight: 300;
		ul {
			display: none;
			top: 1.5em;
			z-index: 99999;
			ul {
				top: 0;
			}
			li {
				padding-left: 1em;
				&:hover > ul,
				&.focus > ul {
					left: 100%;
				}
			}
			a {
				width: 200px;
			}
		}
		li:hover > ul,
		li.focus > ul {
			left: auto;
		}
	}
	li {
		position: relative;
	}
	a {
		display: inline-block;
		width: 100%;
		color: $colour-primary-blue;
		padding: .5em 1em .5em 0;
		text-decoration: none;
	}
	a:hover,
	a:focus {
		color: $color__link-hover;
	}

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a,
	.current-menu-ancestor > a {
	}
	
	.menu-item-has-children,
	.page-item-has-children {
		// min-width: 218px;
	}
	.menu-item-has-children > a,
	.page_item_has_children > a {
		// padding-right: 2em;
	}
}

button.dropdown-toggle {

}

.menu-toggle {

}

/* Toggle small menu and sub-menus on */
.toggled-on ul,
.sub-menu.toggled-on {
	display: block;
}

@media screen and (min-width: $query__small) {
	.menu-toggle {
		display: none;
	}
	
	.main-navigation {
		padding-top: 0;
		.menu-item-has-children > a,
		.page_item_has_children > a {
			// padding-right: 2em;
			// background: hsla(0, 0%, 100%, .1);
		}
		
		ul {
			display: block;
			display: flex;
			flex-wrap: wrap;
		
			ul {
				flex-direction: column;
				margin-left: 0;

				li {
					padding-left: 0;
					
					a {
						width: 218px;
						background: none;
					}
				}
			}
			
			li {
					
				a {
					padding: .4em 1em; 
				}
			}
		}
		
	}
}

@media screen and (min-width: $query__medium) {
	.main-navigation {
		ul {
			justify-content: flex-end;
			padding-top: 0;
		}
	}
}